.table {
  border-collapse: collapse;
  color: white;
  font-size: 1rem;
  margin-top: 3rem;
  width: 95%;
}

.table th {
  font-size: 1.1rem;
}

.table td , .table th {
  border: 1px solid var(--green);
  padding: 8px;
  text-align: center;
}

.table tr:nth-child( even ) {
  background-color: var(--green);
}

.buttons button {
  display: inline;
  margin: 10px;
}

.buttons button:nth-last-child( 1 ) {
  background-color: var(--red);
}
